import getBrowserName from "../../apis/getBrowserName"
import styles from './Navbar.module.scss'
import { useEffect, useState } from "react";
import format from 'date-fns/format'
import useNow from "../../apis/useNow";
import { useAuth } from "../../contexts/AuthContext";

import Alert from '@mui/material/Alert';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

const br = getBrowserName()
let msg = ""
let nono = false
if (br === "chrome") {
  msg = ("  ")
} else if (br === "safari") {
  msg = ("  ")
} else if (br === "edge") {
  msg = ("? ")
} else if (br === "kakao"){
  msg = ("        ")
  nono = true
} else {
  msg = ("     " + br)
  nono = true
}

const randomList = [
  '    .   .',
  '   .    .',
  '          .',
  '    .    .'
]

const BrowserDetect = () => {
  const [open, setOpen] = useState(true);
  const { currentUser } = useAuth()
  const { now } = useNow()
  const [indexMsg, setIndexMsg] = useState(0);

  useEffect(() => {
    setIndexMsg(Math.floor(Math.random() * randomList.length))
  }, [])

  return (
    // nono &&
    <Collapse in={open} sx={{ p:1 }}>
      <Alert severity={nono ? "error" : "success"}
        action={
          <IconButton
            aria-label="close"
            color="inherit"
            size="small"
            onClick={() => {
              setOpen(false);
            }}
          >
            <CloseIcon fontSize="inherit" />
          </IconButton>
        }
      >
        {nono ? msg
          :
          !currentUser ?
          randomList[indexMsg]
          :
          <>
            <span className={ styles.Hello }> ! <b className="accent3">{currentUser.displayName}</b> </span>
            <b className="accent2">{format(now, 'HH:mm:ss')}</b>
            <div>{ randomList[indexMsg]}</div>
          </>
        }
      </Alert>
    </Collapse>
  )
}

export default BrowserDetect;